      }
    },
    setChart: function setChart(data, option, item) {
      if (!item) {
        if (data) {
          this.curItem.config.data = data;
        }

        if (option) {
          this.curItem.config.option = option;
        }
      } else {
        if (data) {
          item.config.data = data;
        }

        if (option) {
