        result = item.config.content ? item.config.content : '这是文本';
      }

      return result;
    },
    addItem: function addItem(type, config) {
      var _this3 = this;

      var item = {
        x: this.layout.length * this.defaultW % this.colNum,
        y: this.layout.length + this.colNum,
        // puts it at the bottom
        w: this.defaultW,
        h: this.defaultH,
