import EChart from '@/components/general/EChart.vue';
export default {
  components: {
    EChart: EChart
  },
  data: function data() {
    return {
      chartData: {},
      chartOption: {}
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.$API.getChart({
      _id: this.$route.params.viewid
    }, function (message) {
      if (message.success) {
        _this.chartData = message.info.data;
        _this.chartOption = message.info.option;
      }
    });
  }
};