import GLayout from '@/components/general/GLayout.vue';
export default {
  components: {
    GLayout: GLayout
  },
  data: function data() {
    return {
      panelBackColor: '',
      panelItemMargin: ''
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.$API.getPanel({
      _id: this.$route.params.viewid
    }, function (message) {
      if (message.success) {
        _this.setLayout(message.info.layout, message.info.back);

        _this.panelBackColor = message.info.back.config.backgroundColor;
        _this.panelItemMargin = message.info.back.config.itemMargin;
      }
    });
  },
  methods: {
    setLayout: function setLayout(layout, back) {
      this.$refs.GLayout.setLayout(layout);
      this.$refs.GLayout.back = back;
    },
    loadData: function loadData(data, itemI, flag) {},
    curItemChange: function curItemChange() {}
  }
};